import * as React from 'react';
import { Block, RenderNodeProps } from '@ali/4ever-cangjie';
import { ColorBlocks, Container } from '@ali/4ever-mo';
import Callout from '../components/calloutMobile';
import { StickerPlugin } from '@ali/4ever-bamboo';
import { Locale } from '../types';

function createRenderNode(param: {
  sticker: StickerPlugin.Sticker;
  locale?: Locale;
}) {
  return {
    [Container.TYPE]: (props: RenderNodeProps<Block>, controller) => {
      const { sticker, locale } = param;
      const { attributes, children } = props;
      const { node } = props;

      if (node.data.subType !== ColorBlocks.SUB_TYPE) {
        return <></>;
      }
      return (
        <Callout
          controller={controller}
          locale={locale}
          node={node}
          attributes={attributes}
          sticker={sticker}
        >
          {children()}
        </Callout>
      );
    },
  };
}

export default createRenderNode;
